(function() {
  'use strict';

  angular
    .module('app.layout')
    .controller('Shell', Shell);

  Shell.$inject = [
    '$timeout',
    '$rootScope',
    'APP_EVENTS',
    'user',
    'dataservice'
  ];

  function Shell(
    $timeout,
    $rootScope,
    APP_EVENTS,
    user,
    dataservice
  ) {
    var vm = this;

    vm.showSplash = false;
    vm.user       = user.info();
    vm.isAdmin    = user.isAdmin();
    vm.isKitchen  = user.isKitchen();
    vm.isManager  = user.isManager();
    vm.logout     = logout;

    activate();

    function activate() {
      // Using a resolver on all routes or dataservice.ready in every controller
      dataservice.ready().then(function() {
        hideSplash();
      });
    }

    function hideSplash() {
      $timeout(function() {
        vm.showSplash = false;
      }, 1000);
    }

    function logout() {
      user.logout();
    }

    // Если пользователь вошел в приложение
    $rootScope.$on(APP_EVENTS.userLogin, function() {
      vm.user      = user.info();
      vm.isAdmin   = user.isAdmin();
      vm.isKitchen = user.isKitchen();
      vm.isManager = user.isManager();
    });
  }
})();
