(function() {
  'use strict';

  angular
    .module('app.auth')
    .controller('Auth', Auth)

  Auth.$inject = [
    '$rootScope',
    'user',
    'splash',
    'logger'
  ];

  function Auth(
    $rootScope,
    user,
    splash,
    logger
  ) {
    let vm  = this;
    vm.form = {
      username: '',
      password: ''
    };

    vm.login = () => {
      user.login(vm.form)
        .then(() => {
          let info = user.info();
          logger.success(`Вхід здійснено успішно: ${info.sub}`);
          splash.delay(1500);

          $rootScope.defaultRedirect();
        })
        .finally(() => {
          vm.form = {
            username: '',
            password: ''
          };
        });
    }
  }
})();