(function() {
  angular.module('app.resources')
    .factory('auth', auth)

  auth.$inject = [
    '$resource'
  ];

  function auth($resource) {
    return $resource('/auth/:action/:data', {}, {
      'login': {
        method: 'POST',
        params: {
          action: ''
        }
      }
    });
  }
})();
