(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('categoriesCreateModal', createCategory)
    .controller('categoriesCreateModalCtrl', createCategoryCtrl);

  createCategory.$inject = [
    'ngDialog',
    'BaseModal'
  ];

  /* @ngInject */
  function createCategory(
    ngDialog,
    BaseModal
  ) {
    // Events that emit controller
    let events = {
      create: 'create'
    };

    let modal   = new BaseModal('createModal', events);
    let service = angular.extend(modal, {
      show: showModal,
      hide: hideModal
    });

    return service;

    ////////////////

    function showModal() {
      ngDialog.open({
        template: 'html/modals/categories/categories.html',
        width: '30%',
        showClose: false,
        controller: 'categoriesCreateModalCtrl',
        controllerAs: 'vm'
      });
    }

    function hideModal() {
      ngDialog.close();
    }
  }

  createCategoryCtrl.$inject = [
    'categories',
    'BaseModal',
    'toastr'
  ];

  function createCategoryCtrl(
    categories,
    BaseModal,
    toastr
  ) {
    let vm    = this;
    let modal = BaseModal.get('createModal');

    vm.create = createCategory;
    vm.form   = {
      name: ''
    };

    ////////////////

    function createCategory() {
      categories.create(vm.form, () => {
        modal.hide();
        toastr.success('Категорія добавлена');

        modal.emit(modal.events.create);
      });
    }
  }
})();
