(function() {
  angular.module('app.core')
    .factory('common', common)

  common.$inject = [
    '$q',
    '$rootScope',
    '$timeout',
  ];

  function common(
    $q,
    $rootScope,
    $timeout
  ) {
    let service = {
      $broadcast: $broadcast,
      $emit: $emit,
      $q: $q,
      $timeout: $timeout
    };

    return service;

    function $broadcast() {
      return $rootScope.$broadcast.apply($rootScope, arguments);
    }

    function $emit() {
      return $rootScope.$emit.apply($rootScope, arguments);
    }
  }
})();