(function() {
  angular.module('app.resources')
    .factory('images', images)

  images.$inject = [
    '$resource'
  ];

  function images($resource) {
    return $resource('/images/:action/:data', {}, {
      'fetchAll': {
        method: 'POST',
        isArray: true,
        params: {
          action: 'getDishImages'
        }
      },
      'upload': {
        method: 'POST',
        params: {
          action: 'addImages'
        }
      }
    });
  }
})();
