(function() {
  angular.module('blocks.splash')
    .directive('splashScreen', splashScreen)
    .factory('splash', splash)

  splash.$inject = [
    'common',
    '$rootScope'
  ];

  function splash(common, $rootScope) {
    let service = {
      show: showScreen,
      hide: hideScreen,
      delay: delayScreen
    };

    return service;

    /**
     * Запускает splash во время выполнения промисов
     * @param {Array} promise
     */

    function showScreen(promise) {
      $rootScope.splashScreen = true;

      common.$q.all(promise).then(() => {
        common.$timeout(() => {
          $rootScope.splashScreen = false;
        }, 600);
      });
    }

    function hideScreen() {
      common.$timeout(() => {
        $rootScope.splashScreen = false;
      }, 600);
    }
    
    function delayScreen(delay) {
      $rootScope.splashScreen = true;
      
      common.$timeout(() => {
        $rootScope.splashScreen = false;
      }, delay);
    }
  }

  function splashScreen() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'html/blocks/splash-screen/splash-screen.html'
    }
  }
})();
