(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('dishesSelectModal', selectDish)
    .controller('dishesSelectModalCtrl', selectDishCtrl);

  selectDish.$inject = [
    'ngDialog',
    'BaseModal'
  ];

  /* @ngInject */
  function selectDish(
    ngDialog,
    BaseModal
  ) {
    // Events that emit controller
    let events = {
      selected: 'selected'
    };

    let modal   = new BaseModal('dishesSelectModal', events);
    let service = angular.extend(modal, {
      show: showModal,
      hide: hideModal
    });

    return service;

    ////////////////

    function showModal() {
      ngDialog.open({
        template: 'html/modals/dishes/select.html',
        showClose: false,
        appendClassName: BaseModal.getClassName() + ' select-modal',
        controller: 'dishesSelectModalCtrl',
        controllerAs: 'vm'
      });
    }

    function hideModal() {
      // TODO грязный хак, dishesSelectModal вызывается поверх другого
      // модального окна
      let dialogs = ngDialog.getOpenDialogs();
      ngDialog.close(dialogs[1]);
    }
  }

  selectDishCtrl.$inject = [
    '$timeout',
    'categories',
    'helpers',
    '$q',
    'BaseModal'
  ];

  function selectDishCtrl(
    $timeout,
    categories,
    helpers,
    $q,
    BaseModal
  ) {
    let vm    = this;
    let modal = BaseModal.get('dishesSelectModal');

    vm.spinner     = true;
    vm.searchDish  = searchDish;
    vm.loadDishes  = loadDishes;
    vm.incQuantity = incQuantity;
    vm.decQuantity = decQuantity;
    vm.pushDishes  = pushDishes;

    vm.listOfDishes = modal.scope.dishes;
    vm.categories   = [];
    vm.dishes       = [];
    vm.selectedCategory;

    $q.all([activate()]).then(() => {
      hideSpinner();
    });

    ////////////////

    function activate() {
      return categories.fetchAll().$promise
        .then(categories => {
          vm.categories = categories;
          return categories[0];
        }).then(firstCategory => {
          return categories.fetchDishes({
            id: firstCategory.id
          }).$promise.then(dishes => {
            vm.selectedCategory = firstCategory;
            vm.dishes           = transformDishesData(dishes);
          });
        });
    }

    function loadDishes(category) {
      vm.spinner = true;

      categories.fetchDishes({
        id: category.id
      }, dishes => {
        vm.selectedCategory = category;
        vm.dishes           = transformDishesData(dishes);

        hideSpinner();
      });
    }

    /**
     * Увеличение кол-во блюда
     * @param {Object} dish
     */

    function incQuantity(dish) {
      // Пытаемся найти переданное блюдо в списке
      let search = searchDish(dish);

      if (search) {
        search.quantity++;
      } else {
        dish.quantity = 1;

        vm.listOfDishes.push(dish);
      }
    }

    /**
     * Уменьшение кол-во блюда
     * @param {Object} dish
     */

    function decQuantity(dish) {
      let search = searchDish(dish);

      if (search) {
        search.quantity--;

        if (!search.quantity) {
          vm.listOfDishes.splice(vm.listOfDishes.indexOf(search), 1);
        }
      }
    }

    function searchDish(dish) {
      return helpers.searchByKey(vm.listOfDishes, {dishId: dish.dishId});
    }

    /**
     * Отправляем список из выбранных блюд
     */

    function pushDishes() {
      modal.emit(modal.events.selected, vm.listOfDishes);
      modal.hide();
    }

    /**
     * Трансформирование данных о блюдах от сервера в более удобный вид
     */

    function transformDishesData(dishes) {
      return dishes.map(dish => {
        return {
          dish: dish,
          dishId: dish.id,
          quantity: 0
        };
      });
    }

    function hideSpinner() {
      $timeout(() => {
        vm.spinner = false;
      }, 400);
    }
  }
})();
