(function() {
  angular.module('app.resources')
    .factory('courier', courier)

  courier.$inject = [
    '$resource'
  ];

  function courier($resource) {
    return $resource('/courier/:action/:data', {}, {
      'fetchAll': {
        method: 'GET',
        isArray: true,
        params: {
          action: 'getAllCouriers'
        }
      }
    });
  }
})();
