(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('dishesEditModal', editDish)
    .controller('dishesEditModalCtrl', dishesEditModalCtrl);

  editDish.$inject = [
    'ngDialog',
    'BaseModal'
  ];

  /* @ngInject */
  function editDish(
    ngDialog,
    BaseModal
  ) {
    // Events that emit controller
    let events = {
      update: 'update'
    };

    let modal   = new BaseModal('dishesEditModal', events);
    let service = angular.extend(modal, {
      show: showModal,
      hide: hideModal
    });

    return service;

    ////////////////

    function showModal() {
      ngDialog.open({
        template: 'html/modals/dishes/edit.html',
        width: '60%',
        showClose: false,
        controller: 'dishesEditModalCtrl',
        controllerAs: 'vm'
      });
    }

    function hideModal() {
      ngDialog.close();
    }
  }

  dishesEditModalCtrl.$inject = [
    'dishes',
    'images',
    'BaseModal',
    'toastr'
  ];

  function dishesEditModalCtrl(
    dishes,
    images,
    BaseModal,
    toastr
  ) {
    let vm    = this;
    let modal = BaseModal.get('dishesEditModal');

    vm.update  = updateDish;
    vm.spinner = false;
    vm.form    = {
      images: [],
      id: modal.scope.id,
      name: modal.scope.name,
      price: modal.scope.price,
      description: modal.scope.description,
      type: modal.scope.type,
      categoryId: modal.scope.categoryId
    };

    vm.types = [{
      name: 'Звичайний',
      value: 'ORDINARY'
    }, {
      name: 'Новинка',
      value: 'LATEST'
    }, {
      name: 'Гостра страва',
      value: 'HOT'
    }, {
      name: 'Вегетаріанська страва',
      value: 'VEGAN'
    }];

    ////////////////

    activate();

    /**
     * Загружает изображения блюда
     */

    function activate() {
      vm.spinner = true;

      let obj = {
        dishId: modal.scope.id,
        links: []
      };

      modal.scope.images.forEach(image => {
        obj.links.push(image.link);
      });

      images.fetchAll([obj], images => {
        vm.spinner     = false;
        vm.form.images = images[0].images;
      });
    }

    function updateDish() {
      vm.spinner = true;
      dishes.update(vm.form, updateDish => {
        vm.spinner = false;

        modal.hide();
        toastr.success('Страву обновлено');

        modal.emit(modal.events.update, updateDish);
      }, () => {
        vm.spinner = false;
      });
    }
  }
})();