(function() {
  'use strict';

  angular
    .module('app.layout')
    .controller('Header', Header);

  Header.$inject = [
    'orderHistoryIntervalModal',
    'managerOrderDetailModal',
    'helpers',
    'USER_ROLES',
    'user'
  ];

  function Header(
    intervalModal,
    orderDetailModal,
    helpers,
    USER_ROLES,
    user
  ) {
    var vm = this;

    vm.roleName       = getRoleName();
    vm.orderHistory   = intervalModal.show;
    vm.createNewOrder = showModalOrder;

    ////////////////

    function getRoleName() {
      let result = 'адміністратор',
          roles  = user.roles();

      if (helpers.findString(roles, USER_ROLES.manager)) {
        result = 'менеджер';
      }

      if (helpers.findString(roles, USER_ROLES.kitchen)) {
        result = 'кухня';
      }

      return result;
    }

    function showModalOrder() {
      orderDetailModal.scope.createEnabled = true;
      orderDetailModal.show();
    }
  }
})();
