(function() {
  'use strict';

  angular
    .module('blocks.image-upload', [])
    .directive('imageUpload', imageUpload)

  imageUpload.$inject = [
    'toastr',
    '$q'
  ];

  function imageUpload(
    toastr,
    $q
  ) {
    return {
      restrict: 'E',
      replace: true,
      scope: {},
      require: 'ngModel',
      templateUrl: 'html/blocks/image-upload/image-upload.html',
      link: function(scope, element, attrs, ngModel) {
        scope.uploaded    = false;
        scope.imagesDish  = [];
        scope.removeImage = removeImage;

        // ngModel.$render = function() {
        //   // Если указаны изображения (форма редактирования)
        //   if (ngModel.$viewValue.length) {
        //     scope.uploaded   = true;
        //     scope.imagesDish = ngModel.$viewValue;
        //     ngModel.$setValidity('required', true);
        //   } else {
        //     ngModel.$setValidity('required', false);
        //   }
        // };
        //
        // ngModel.$render();

        // scope.$watch(function() {
        //   return ngModel.$modelValue;
        // }, function(newValue) {
        //   // Если указаны изображения (форма редактирования)
        //   if (newValue.length) {
        //     scope.uploaded   = true;
        //     scope.imagesDish = ngModel.$viewValue;
        //   }
        //
        //   ngModel.$render();
        // });

        let input = element.find('input');
        let opts  = {
          max_filesize: 2 * 1024 * 1024,
          types: ['image/png', 'image/jpg', 'image/jpeg']
        };

        ////////////////

        input.on('change', function(changeEvent) {
          let files = [];

          angular.forEach(changeEvent.target.files, file => {
            files.push(file);
          });

          let validator = validate(files);

          if (validator.status) {
            $q.all([uploadBinary(files)]).then(binaries => {
              scope.imagesDish = binaries[0];
              scope.uploaded   = true;

              ngModel.$setViewValue(files);
              ngModel.$setValidity('arrayRequired', true);
              // toastr.success('Изображения загружены');
            });
          } else {
            printError(validator);
          }
        });

        function uploadBinary(files) {
          var deferred      = $q.defer();
          let countUploaded = 0;
          let result        = [];

          for (let i = 0; i < files.length; i++) {
            let dataURL = new FileReader();

            dataURL.onload = (readerEvt) => {
              countUploaded++;

              result.push(btoa(readerEvt.target.result));

              if (countUploaded === files.length) {
                deferred.resolve(result);
              }
            };

            dataURL.readAsBinaryString(files[i]);
          }

          return deferred.promise;
        }

        function removeImage(image) {
          scope.imagesDish.splice(scope.imagesDish.indexOf(image), 1);

          if (!scope.imagesDish.length) {
            scope.uploaded = false;
            ngModel.$setValidity('arrayRequired', false);
          }

          ngModel.$setViewValue(scope.imagesDish);
        }

        function validate(files) {
          let valid = {
            status: true,
            reason: null
          };

          for (let i = 0; i < files.length; i++) {
            if (files[i].size > opts.max_filesize) {
              valid.status = false;
              valid.reason = 'max_filesize'
            }

            if (opts.types.indexOf(files[i].type) === -1) {
              valid.status = false;
              valid.reason = 'format'
            }
          }

          return valid;
        }

        function printError(validate) {
          let errorMsg = '';

          switch (validate.reason) {
            case 'max_filesize':
              errorMsg = 'Максимальний розмір файла 2Мб';
              break;
            case 'format':
              errorMsg = 'Заборонений тип файлу';
              break;
          }

          toastr.error(errorMsg);
        }
      }
    }
  }
})();