(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('dishesCreateModal', createDish)
    .controller('dishesCreateModalCtrl', createDishCtrl);

  createDish.$inject = [
    'ngDialog',
    'BaseModal'
  ];

  /* @ngInject */
  function createDish(
    ngDialog,
    BaseModal
  ) {
    // Events that emit controller
    let events = {
      create: 'create'
    };

    let modal   = new BaseModal('dishesCreateModal', events);
    let service = angular.extend(modal, {
      show: showModal,
      hide: hideModal
    });

    return service;

    ////////////////

    function showModal() {
      ngDialog.open({
        template: 'html/modals/dishes/create.html',
        width: '60%',
        showClose: false,
        controller: 'dishesCreateModalCtrl',
        controllerAs: 'vm'
      });
    }

    function hideModal() {
      ngDialog.close();
    }
  }

  createDishCtrl.$inject = [
    'dishes',
    'images',
    'BaseModal',
    'toastr'
  ];

  function createDishCtrl(
    dishes,
    images,
    BaseModal,
    toastr
  ) {
    let vm    = this;
    let modal = BaseModal.get('dishesCreateModal');

    vm.create  = createDish;
    vm.spinner = false;
    vm.form    = {
      images: [],
      name: '',
      price: '',
      description: '',
      type: 'ORDINARY',
      categoryId: modal.scope.selectedCategory.id
    };

    vm.types = [{
      name: 'Звичайний',
      value: 'ORDINARY'
    }, {
      name: 'Новинка',
      value: 'LATEST'
    }, {
      name: 'Гостра страва',
      value: 'HOT'
    }, {
      name: 'Вегетаріанська страва',
      value: 'VEGAN'
    }];

    ////////////////

    function createDish() {
      vm.spinner = true;
      console.log(vm.form);

      dishes.create(vm.form, created => {
        let fd = new FormData();
        fd.append('id', created.id);
        fd.append('type', 'dish');

        vm.form.images.forEach(image => {
          console.log(image);
          fd.append('images[]', image);
        });

        images.upload(fd, () => {
          vm.spinner = false;

          modal.hide();
          toastr.success('Страву добавлена');

          modal.emit(modal.events.create);
        });
      });
    }
  }
})();
