(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('orderHistoryModal', orderHistory)
    .controller('orderHistoryModalCtrl', orderHistoryCtrl);

  orderHistory.$inject = [
    'ngDialog',
    'BaseModal'
  ];

  /* @ngInject */
  function orderHistory(
    ngDialog,
    BaseModal
  ) {

    let modal   = new BaseModal('orderHistoryModal');
    let service = angular.extend(modal, {
      show: showModal,
      hide: hideModal
    });

    return service;

    ////////////////

    function showModal() {
      ngDialog.open({
        template: 'html/modals/history/history.html',
        showClose: false,
        appendClassName: BaseModal.getClassName() + ' orders-history',
        controller: 'orderHistoryModalCtrl',
        controllerAs: 'vm'
      });
    }

    function hideModal() {
      ngDialog.close();
    }
  }

  orderHistoryCtrl.$inject = [
    'BaseModal',
    'orderHistoryIntervalModal'
  ];

  function orderHistoryCtrl(
    BaseModal,
    intervalModal
  ) {
    let vm    = this;
    let modal = BaseModal.get('orderHistoryModal');

    vm.orders            = addShowDetailProperty(modal.scope.orders);
    vm.hideBack          = modal.scope.hideBack;
    vm.showIntervalModal = showIntervalModal;
    vm.toggleOrderDetail = toggleOrderDetail;

    ////////////////

    function showIntervalModal() {
      modal.hide();
      intervalModal.show();
    }

    function addShowDetailProperty(orders) {
      return orders.map(order => {
        return angular.extend(order, {
          showDetail: false
        });
      });
    }

    function toggleOrderDetail(order) {
      order.showDetail = !order.showDetail;
    }
  }
})();

