(function() {
  angular.module('app.resources')
    .factory('admin', admin)

  admin.$inject = [
    '$resource'
  ];

  function admin($resource) {
    return $resource('/admin/:action/:data', {}, {
      'create': {
        method: 'POST',
        params: {
          action: 'create'
        }
      },
      'fetchAll': {
        method: 'GET',
        isArray: true,
        params: {
          action: 'getAll'
        }
      },
      'history': {
        method: 'GET',
        isArray: true,
        params: {
          action: 'getOrderHistory'
        }
      },
      'declineOrder': {
        method: 'POST',
        params: {
          action: 'declineOrder'
        }
      },
      'makeInCooking': {
        method: 'POST',
        params: {
          action: 'proceedToCooking'
        }
      },
      'setOrderToCourier': {
        method: 'POST',
        params: {
          action: 'setOrderToCourier'
        }
      },
      'passOrderToCourier': {
        method: 'POST',
        params: {
          action: 'passOrderToCourier'
        }
      },
      'releaseCourier': {
        method: 'POST',
        params: {
          action: 'releaseCourier'
        }
      },
      'courierOnPlace': {
        method: 'POST',
        params: {
          action: 'courierOnPlace'
        }
      }
    });
  }
})();
