(function() {
  'use strict';

  angular
    .module('app.resources')
    .factory('categories', categories);

  categories.$inject = [
    '$resource'
  ];

  /* @ngInject */
  function categories($resource) {
    return $resource('/category/:action/:id', {}, {
      'fetchAll': {
        method: 'GET',
        isArray: true,
        params: {
          action: 'getAll'
        }
      },
      'fetchDishes': {
        method: 'GET',
        isArray: true,
        params: {
          action: 'getAllDishesFromCategory'
        }
      },
      'create': {
        method: 'POST',
        params: {
          action: 'create'
        }
      },
      'remove': {
        method: 'GET',
        params: {
          action: 'remove'
        }
      },
      'removeDish': {
        method: 'POST',
        params: {
          action: 'removeDishFromCategory'
        }
      }
    });
  }
})();

