(function() {
  angular.module('app', [
      'app.core',
      'app.services',
      'app.resources',
      'app.modals',
      'app.filters',

      /*
       * Feature areas
       */

      'app.admin.panel',
      'app.admin.dashboard',
      'app.manager.dashboard',
      'app.kitchen.orders',
      'app.auth',
      'app.layout'
    ])
    .config((
      localStorageServiceProvider,
      $anchorScrollProvider,
      ngDialogProvider,
      $httpProvider
    ) => {
      localStorageServiceProvider
        .setPrefix('YOKI-CRM')
        .setStorageType('localStorage')
        .setNotify(true, true);

      ngDialogProvider.setDefaults({
        showClose: true
      });

      $httpProvider.interceptors.push('apiInterceptor');
      $anchorScrollProvider.disableAutoScrolling();

      $httpProvider.defaults.useXDomain                         = true;
      $httpProvider.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
      $httpProvider.defaults.headers.post['Content-Type']       = 'application/json';
      $httpProvider.defaults.headers.post['Accept']             = 'application/json';
    })
    .run((
      $rootScope,
      $location,
      user,
      USER_ROLES,
      helpers
    ) => {
      $rootScope.location = $location;
      $rootScope.go       = (path) => {
        $location.path(path);
      }

      $rootScope.defaultRedirect = () => {
        if (helpers.findString(user.roles(), USER_ROLES.admin)) {
          $location.path('/admin/dashboard');
          return;
        }

        if (helpers.findString(user.roles(), USER_ROLES.manager)) {
          $location.path('/manager/dashboard');
          return;
        }

        if (helpers.findString(user.roles(), USER_ROLES.kitchen)) {
          $location.path('/kitchen/orders');
          return;
        }
      }
    })
})();
