(function() {
  angular.module('app.resources')
    .factory('orders', orders)

  orders.$inject = [
    '$resource'
  ];

  function orders($resource) {
    return $resource('/operator/:action/:data', {}, {
      'create': {
        method: 'POST',
        params: {
          action: 'create'
        }
      },
      'fetchAll': {
        method: 'GET',
        isArray: true,
        params: {
          action: 'getAll'
        }
      },
      'makeInProgress': {
        method: 'POST',
        params: {
          action: 'setOrderInProgress'
        }
      },
      'close': {
        method: 'POST',
        params: {
          action: 'closeOrder'
        }
      },
      'decline': {
        method: 'POST',
        params: {
          action: 'declineOrder'
        }
      },
      'update': {
        method: 'POST',
        params: {
          action: 'updateOrder'
        }
      },
      'create': {
        method: 'POST',
        params: {
          action: 'createOrder'
        }
      }
    });
  }
})();
