(function() {
  angular.module('app.services')
    .factory('user', user)

  /**
   * @ngdoc service
   * @name user
   * @module app.services
   *
   * @description
   * Сервис который отвечает за авторизацию пользователей
   */

  user.$inject = [
    '$q',
    '$rootScope',
    'APP_EVENTS',
    'USER_ROLES',
    '$location',
    'jwtHelper',
    'helpers',
    'auth',
    'storage'
  ];

  function user(
    $q,
    $rootScope,
    APP_EVENTS,
    USER_ROLES,
    $location,
    jwtHelper,
    helpers,
    auth,
    storage
  ) {
    let isAuthenticated = false;
    let service         = {
      login: login,
      logout: logout,
      info: getInfo,
      roles: getRoles,
      isAdmin: isAdmin,
      isManager: isManager,
      isKitchen: isKitchen,
      isAuth() {
        return isAuthenticated;
      }
    };

    isAuth();

    return service;

    ////////////////

    function isAuth() {
      let token = storage.get('token');

      if (token) {
        isAuthenticated = true;
      } else {
        isAuthenticated = false;
      }
    }

    function login(form) {
      var deferred = $q.defer();

      auth.login(form, response => {
        if (exceptCourier(response.token)) {
          storage.set('token', response.token);
          isAuthenticated = true;
          $rootScope.$broadcast(APP_EVENTS.userLogin);

          deferred.resolve(response);
        } else {
          deferred.reject();
        }
      }, error => {
        deferred.reject(error);
      });

      return deferred.promise;
    }

    function logout() {
      isAuthenticated = false;
      storage.remove('token');

      $location.path('/auth');
    }

    function getInfo() {
      let token = storage.get('token');

      if (token) {
        return jwtHelper.decodeToken(token);
      }
    }

    function getRoles() {
      let info   = getInfo();
      let result = [];

      if (info) {
        info.userRoles.forEach((role) => {
          result.push(role.name);
        });
      } else {
        result.push(USER_ROLES.guest);
      }

      return result;
    }

    function isAdmin() {
      return isRole(USER_ROLES.admin);
    }

    function isManager() {
      return isRole(USER_ROLES.manager);
    }

    function isKitchen() {
      return isRole(USER_ROLES.kitchen);
    }

    function isRole(role) {
      let roles  = getRoles();
      let result = false;

      if (roles) {
        if (helpers.findString(roles, role)) {
          result = true;
        }
      }

      return result;
    }

    function exceptCourier(token) {
      let info   = jwtHelper.decodeToken(token);
      let result = true;

      info.userRoles.forEach(role => {
        if (role.name === 'ROLE_COURIER') {
          result = false;
        }
      });

      return result;
    }
  }
})();