(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('BaseModal', BaseModal)

  BaseModal.$inject = ['user'];

  function BaseModal(user) {
    let cache = [];

    class BaseModalClass {
      /**
       * Базовый класс для модальных окон
       * @param name
       * @param events
       * @description содержит публичные свойства events, scope. Scope используется
       * для передачи в контроллер модального окна данных из внешнего scope.
       */

      constructor(name, events) {
        this._subscribers = {};

        this.events = events || {};
        this.scope  = {};

        cache.push({
          [name]: this
        });
      }

      /**
       * Подписка на события в модальном окне
       * @param {String} eventName
       * @param {Function} cb
       */

      on(eventName, cb) {
        this._subscribers[eventName] = cb;
      }

      /**
       * Вызов всех подписчиков на переданное событие
       * @param {String} eventName
       * @param {Array} args
       */

      emit(eventName, ...args) {
        let sub = this._subscribers[eventName];
        sub.apply(sub, args);
      }

      /**
       * Возврат модального окна из кеша если есть
       * @param {String} name
       * @returns {Object}
       */

      static get(name) {
        let result = {};

        cache.forEach(modal => {
          if (modal[name]) {
            result = modal[name];
          }
        });

        return result;
      }

      static getClassName() {
        return (user.isManager()) ? 'manager-modal' : '';
      }
    }

    return BaseModalClass;
  }
})();