(function() {
  angular.module('blocks.helpers', [])
    .factory('helpers', helpers)

  helpers.$inject = [];

  function helpers() {

    var service = {};

    /**
     * Возвращает новый массив разделенные на ряды, в каждом ряду будет sizeColumn колонок
     * Helpers.shapeArrayInRows([0,1,2,3], 3) => [[0,1,2],[3]]
     * @param {Array} array
     * @param {Integer} sizeColumn
     * @returns {Array}
     */

    service.shapeArrayInRows = (array, sizeColumn) => {
      var result = [];

      for (var i = 0; i < array.length; i += sizeColumn) {
        result.push(array.slice(i, i + sizeColumn));
      }

      return result;
    }

    /**
     * Выполняет поиск в массиве data, по переданному объекту conditions.
     * Пример объекта conditions = {key: value}
     * @param {Array} data
     * @param {Object} conditions
     * @returns {Object|Undefined}
     */

    service.searchByKey = (data, conditions) => {
      let keys = Object.keys(conditions);
      let result;

      data.forEach((row) => {
        if (row[keys[0]] == conditions[keys[0]]) {
          result = row;
        }
      });

      return result;
    }

    service.searchIndex = (data, conditions) => {
      let keys = Object.keys(conditions);
      let index;

      for (let i = 0; i < data.length; i++) {
        if (data[i][keys[0]] == conditions[keys[0]]) {
          index = i;
        }
      }

      return index;
    }

    service.nextElementInArray = (data, conditions) => {
      let keys = Object.keys(conditions);
      let result;

      data.forEach((row, i, array) => {
        if (row[keys[0]] == conditions[keys[0]]) {
          result = array[i + 1];
        }
      });

      return result;
    }

    service.prevElementInArray = (data, conditions) => {
      let keys = Object.keys(conditions);
      let result;

      data.forEach((row, i, array) => {
        if (row[keys[0]] == conditions[keys[0]]) {
          result = array[i - 1];
        }
      });

      return result;
    }

    /**
     * Ищет переданную строку в массиве из строк
     * @param array
     * @param string
     * @returns {*}
     */

    service.findString = (array, string) => {
      let result = false;

      array.forEach((item) => {
        if (item === string) {
          result = item;
        }
      });

      return result;
    }

    /**
     * Сортировка сущностей по id в порядке убывания
     * @param prev
     * @param next
     * @returns {number}
     */

    service.compareById = (prev, next) => {
      if (prev.id > next.id) {
        return -1;
      }

      if (prev.id < next.id) {
        return 1;
      }

      return 0;
    }

    /**
     * Фильтрация всех заказов по переданным статусам
     * @param {Array} orders
     * @param {Array} statuses
     * @returns {Array}
     */

    service.filterOrdersByStatus = (orders, statuses) => {
      let results = [];

      orders.forEach((order) => {
        if (service.findString(statuses, order.status)) {
          results.push(order);
        }
      });

      return results;
    }

    /**
     * Подсчитывает сводную информацию о заказе
     * @param order
     * @returns {{price: number, count: number}}
     */

    service.calculateOrderInfo = (order) => {
      let result = {
        price: 0,
        count: 0
      };

      for (let dish of order.dishes) {
        result.price += dish.dish.price * dish.quantity;
        result.count += dish.quantity;
      }

      return result;
    }

    /**
     * Получает текущую дату и возвращает объект
     * @returns {{yyyy: number, mm: number, dd: number}}
     */

    service.getDate = () => {
      let today = new Date();
      let dd    = today.getDate();
      let mm    = today.getMonth() + 1;
      let yyyy  = today.getFullYear();

      if (dd < 10) {
        dd = '0' + dd
      }

      if (mm < 10) {
        mm = '0' + mm
      }

      let str = `${yyyy}-${mm}-${dd}`;

      return {
        yyyy,
        mm,
        dd,
        str
      }
    }

    /**
     * Делит строку вида 23:59 и возвращает объект
     * @param str
     * @returns {{hours: string, minutes: string}}
     */

    service.getHourMinutes = (str) => {
      if (str) {
        return {
          hours: str.substr(0, 2),
          minutes: str.substr(2, 4)
        }
      }
    }

    return service;
  }
})();
