(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('orderHistoryIntervalModal', orderHistoryInterval)
    .controller('orderHistoryIntervalModalCtrl', orderHistoryIntervalCtrl);

  orderHistoryInterval.$inject = [
    'ngDialog',
    'BaseModal'
  ];

  /* @ngInject */
  function orderHistoryInterval(
    ngDialog,
    BaseModal
  ) {

    let modal   = new BaseModal('orderHistoryIntervalModal');
    let service = angular.extend(modal, {
      show: showModal,
      hide: hideModal
    });

    return service;

    ////////////////

    function showModal() {
      ngDialog.open({
        template: 'html/modals/history/interval.html',
        width: '30%',
        showClose: false,
        appendClassName: BaseModal.getClassName(),
        controller: 'orderHistoryIntervalModalCtrl',
        controllerAs: 'vm'
      });
    }

    function hideModal() {
      ngDialog.close();
    }
  }

  orderHistoryIntervalCtrl.$inject = [
    'admin',
    'orderHistoryModal',
    'BaseModal'
  ];

  function orderHistoryIntervalCtrl(
    admin,
    historyModal,
    BaseModal
  ) {
    let vm    = this;
    let modal = BaseModal.get('orderHistoryIntervalModal');

    vm.spinner    = false;
    vm.getOrders  = getOrders;
    vm.form       = {
      start: '',
      finish: ''
    };

    ////////////////

    function getOrders() {
      vm.spinner = true;

      let sendData = {
        startDate: vm.form.start + ' 00:00:00',
        endDate: vm.form.finish + ' 00:00:00'
      };

      admin.history(sendData, result => {
        vm.spinner         = false;
        historyModal.scope = {
          orders: result
        };

        modal.hide();
        historyModal.show();
      });
    }
  }
})();