(function() {
  'use strict';

  angular
    .module('app.auth')
    .run(appRun);

  /* @ngInject */
  function appRun(routehelper, USER_ROLES) {
    routehelper.configureRoutes(getRoutes(USER_ROLES));
  }

  /* @ngInject */
  function getRoutes(USER_ROLES) {
    return [
      {
        url: '/auth',
        config: {
          templateUrl: 'html/components/common/auth/auth.html',
          controller: 'Auth',
          controllerAs: 'auth',
          data: {
            authorities: [
              USER_ROLES.guest
            ]
          }
        }
      }
    ];
  }
})();
