(function() {
  angular.module('app.resources')
    .factory('kitchen', kitchen)

  kitchen.$inject = [
    '$resource'
  ];

  function kitchen($resource) {
    return $resource('/kitchen/:action/:data', {}, {
      'fetchAll': {
        method: 'GET',
        isArray: true,
        params: {
          action: 'getOrders'
        }
      },
      'prepared': {
        method: 'POST',
        params: {
          action: 'orderPrepared'
        }
      },
      'notPrepared': {
        method: 'POST',
        params: {
          action: 'orderNotPrepared'
        }
      }
    });
  }
})();
