(function() {
  angular.module('app.resources')
    .factory('dishes', dishes)

  dishes.$inject = [
    '$resource'
  ];

  function dishes($resource) {
    return $resource('/dish/:action/:data', {}, {
      'create': {
        method: 'POST',
        params: {
          action: 'create'
        }
      },
      'update': {
        method: 'POST',
        params: {
          action: 'update'
        }
      },
      'activate': {
        method: 'POST',
        params: {
          action: 'activate'
        }
      },
      'deactivate': {
        method: 'POST',
        params: {
          action: 'deactivate'
        }
      },
    });
  }
})();
