(function() {
  'use strict';

  angular
    .module('blocks.edit-field', [])
    .directive('editField', editField)

  editField.$inject = [
    'toastr'
  ];

  function editField(toastr) {
    return {
      restrict: 'E',
      replace: true,
      transclude: true,
      scope: {
        placeholder: '@',
        mask: '@'
      },
      require: 'ngModel',
      templateUrl: 'html/blocks/edit-field/edit-field.html',
      link: function(scope, element, attrs, ngModel) {
        scope.editMode = false;

        scope.editModeOn = () => {
          let queryResult  = element[0].getElementsByClassName('edit-field-input');
          let spanElements = angular.element(queryResult);

          console.log(spanElements);
          
          scope.fieldData = ngModel.$viewValue;
          scope.editMode  = true;
        }

        scope.editModeOff = () => {
          scope.fieldData = ngModel.$viewValue;
          scope.editMode  = false;
        }

        scope.editSave = () => {
          if (!scope.fieldData) {
            toastr.error('Поле не повинно бути порожнім!');
          } else {
            ngModel.$setViewValue(scope.fieldData);
            ngModel.$render();
            scope.editModeOff();
          }
        }

        let queryResult  = element[0].getElementsByClassName('field-data-value');
        let spanElements = angular.element(queryResult);

        spanElements.bind('click', () => {
          scope.$apply(function() {
            scope.editModeOn();
          });

          event.preventDefault();
        });

        element.bind('keydown keypress', (event) => {
          if (event.which === 13) {
            scope.$apply(function() {
              scope.editSave();
            });

            event.preventDefault();
          }
        });

        ngModel.$render = function() {
          scope.fieldData = ngModel.$viewValue;
        };

        ngModel.$render();
      }
    }
  }

})();