(function() {
  angular.module('blocks.validator')
    .factory('validator', validator)

  validator.$inject = [];

  function validator() {
    let validators = {
      required: required,
      phone: phone
    };

    let service = {
      run: runValidator
    };

    return service;

    ////////////////

    function runValidator(object, rules) {
      let report = {
        errors: []
      };

      rules.forEach(ruleObj => {
        if (object.hasOwnProperty(ruleObj.field)) {
          let value = object[ruleObj.field];

          ruleObj.rules.forEach(rule => {
            if (!validators[rule](value)) {
              report.errors.push({
                field: ruleObj.field,
                rule: rule
              });
            }
          });
        }
      });

      return report;
    }

    function required(field) {
      let result = false;
      
      if (Array.isArray(field)) {
        if (field.length) {
          result = true;
        }
      } else {
        if (field) {
          result = true;
        }
      }

      return result;
    }

    function phone(field) {
      let result = false;
      
      if (field.length === 10) {
        result = true;
      }

      return result;
    }
  }
})();

