(function() {
  'use strict';

  /**
   * @description
   * Основная конфигурация приложения
   */

  var core = angular.module('app.core');

  var config = {
    appErrorPrefix: '[YOKI-CRM Error] ',
    appTitle: 'YOKI CRM',
    version: '1.0.0'
  };

  core.value('config', config);

  core.config(toastrConfig);
  core.config(configure);
  core.config(attachAuthToken);
  
  core.run(defaultRoute);
  core.run(secureRoutes);

  /* @ngInject */
  function toastrConfig(toastr) {
    toastr.options.timeOut       = 3000;
    toastr.options.positionClass = 'toast-bottom-right';
  }

  configure.$inject = [
    '$logProvider',
    '$routeProvider',
    'routehelperConfigProvider',
    'exceptionHandlerProvider'
  ];

  function configure(
    $logProvider,
    $routeProvider,
    routehelperConfigProvider,
    exceptionHandlerProvider
  ) {
    // turn debugging off/on (no info or warn)
    if ($logProvider.debugEnabled) {
      $logProvider.debugEnabled(true);
    }

    // Configure the common route provider
    routehelperConfigProvider.config.$routeProvider = $routeProvider;
    var resolveAlways                               = {
      /* @ngInject */
      ready: function(dataservice) {
        return dataservice.ready();
      }
    };

    routehelperConfigProvider.config.resolveAlways = resolveAlways;

    // Configure the common exception handler
    exceptionHandlerProvider.configure(config.appErrorPrefix);
  }

  defaultRoute.$inject = [
    '$rootScope',
    '$route',
    'user'
  ];

  function defaultRoute(
    $rootScope,
    $route,
    user
  ) {
    $rootScope.$on('$locationChangeStart', (event) => {
      let route = ($route.routes[$rootScope.location.path()]);

      // Если не найден роут, значит вычисляем $routeProvider.otherwise
      if (!route) {
        event.preventDefault();

        if (!user.isAuth()) {
          $rootScope.go('/auth');
        } else {
          $rootScope.defaultRedirect();
        }
      }
    });
  }

  secureRoutes.$inject = [
    '$rootScope',
    '$route',
    'user',
    'helpers',
    'logger'
  ];

  function secureRoutes(
    $rootScope,
    $route,
    user,
    helpers,
    logger
  ) {
    // Запрещаем прямой доступ к защищенным урлам
    $rootScope.$on('$locationChangeStart', (event) => {
      let route = ($route.routes[$rootScope.location.path()]);

      if ((route.data) && (route.data.authorities)) {
        let access = false;

        route.data.authorities.forEach((role) => {
          if (helpers.findString(user.roles(), role)) {
            access = true;
          }
        });

        if (!access) {
          event.preventDefault();
          $rootScope.defaultRedirect();
          logger.error('Помилка доступу');
        }
      }
    });
  }

  attachAuthToken.$inject = [
    'ENV',
    '$httpProvider',
    'jwtInterceptorProvider'
  ];

  function attachAuthToken(
    ENV,
    $httpProvider,
    jwtInterceptorProvider
  ) {
    jwtInterceptorProvider.tokenGetter = [
      'jwtHelper',
      '$http',
      'storage',
      '$location',
      function(jwtHelper, $http, storage, $location) {
        var token = storage.get('token');

        if (angular.isDefined(token)) {
          if (jwtHelper.isTokenExpired(token)) {
            return $http({
              url: ENV.apiUrl + '/auth/refresh',
              skipAuthorization: false,
              method: 'POST',
              headers: {
                Authorization: 'Bearer ' + token
              }
            }).then(function(response) {
              var newToken = response.data.token;

              storage.set('token', newToken);
              return newToken;
            }, function() {
              $location.path('/logout');
            });
          } else {
            return token;
          }
        }
      }];

    $httpProvider.interceptors.push('jwtInterceptor');
  }
})();
