(function() {
  'use strict';

  angular
    .module('app.admin.panel')
    .run(appRun);

  /* @ngInject */
  function appRun(routehelper, USER_ROLES) {
    routehelper.configureRoutes(getRoutes(USER_ROLES));
  }

  function getRoutes(USER_ROLES) {
    return [
      {
        url: '/admin/panel',
        config: {
          templateUrl: 'html/components/admin/panel/panel.html',
          controller: 'Panel',
          controllerAs: 'panel',
          data: {
            authorities: [
              USER_ROLES.admin
            ]
          }
        }
      }
    ];
  }
})();
